* {
  --github-color: #24292e;
  --highlight-color: #ebc90b;
  --menu-height: 55px;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
  text-align: center;
}

/* =============== MENU =============== */
.menu {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2f274d;
  width: 100%;
  height: var(--menu-height);
  z-index: 1;
}

.menu__open-button {
  display: none;
  color: white;
  cursor: pointer;
  font-size: 20px;
}

.menuSections {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.menu__options {
  display: flex;
  height: 55px;
  align-items: center;
  justify-content: center;
}

.menu__item {
  text-decoration: none;
  margin: 0 15px;
  color: white;
  font-weight: bold;
  padding-bottom: 3px;
  border-bottom: 1px solid #fff;
  transition: all .5s;
}

.menu__item--img {
  border-bottom: none;
  padding-bottom: 0;
}

.menu__logo {
  width: 35px;
}

.menu__item:hover {
  transform: translateY(-5px);
  color: var(--highlight-color);
  border-bottom: 0;
}

/* =============== INTRODUCING BANNER =============== */
.banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #171e25;
  padding: calc(var(--menu-height) + 45px) 0 45px 0;
  color: white;
  transition: all 1s;
}

.banner:hover {
  background-color: #3f454b;
}

.banner__lang {
  position: absolute;
  top: var(--menu-height);
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  padding: 10px;
  color: white;
}

.banner__header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 55px
}

.banner__welcome {
  margin-top: 10px;
}

.banner__github-logo {
  width: 65px;
  border: 3px solid white;
  background-color: white;
  border-radius: 99px;
  transition: all .5s;
}

.banner__github-logo:hover {
  border-color: var(--highlight-color);
}

.banner__skills {
  width: 100%;
  margin-top: 45px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.banner__service-img {
  height: 95px;
}

.banner__service-text {
  margin-top: 10px;
}

/* =============== PROJECTS SECTION =============== */
.projects {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #e6e6e6;
  transition: all 1s;
}

.projects:hover {
  background-color: #bed7f3;
}

.projects__visible {
  margin: 25px 0;
}

.projects__section {
  margin-top: 45px;
}

.projects__list {
  display: grid;
  grid-template-columns: 32% 32% 32%;
  align-items: center;
  gap: 15px;
  margin: 15px 35px;
}

.project {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 320px;
  background-color: white;
  border-radius: 8px;
  padding: 10px;
  box-shadow: 0 1px 1px rgba(0,0,0,0.12),
              0 2px 2px rgba(0,0,0,0.12),
              0 4px 4px rgba(0,0,0,0.12),
              0 8px 8px rgba(0,0,0,0.12),
              0 16px 16px rgba(0,0,0,0.12);
}

.project__header {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 25px
}

.project__link--link {
  cursor: pointer;
  text-decoration: none;
  transition: all .6s;
}

.project__link--link:hover {
  transform: scale(1.2);
}

.project__plus {
  font-size: 28px;
  color: #2f274d;
}

.project__info {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 35px;
}

.project__img {
  height: 95px;
}

.project__img--portfolio {
  background-color: #242a3a;
  padding: 5px;
  border-radius: 15px;
}

.project__frameworks {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 15px;
}

.project__description {
  text-align: justify;
  padding: 0 10px;
}

.project__framework-logo {
  width: 25px;
  padding: 0 5px;
}

.project__gh-button, .project__private-button {
  width: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-weight: bold;
  background-color: var(--github-color);
  padding: 10px;
  border-radius: 8px;
  color: white;
  border: 2px solid white;
  transition: all .5s;
}

.project__private-button {
  background-color: #3e3074;
  opacity: .6;
  border: 0;
}

.project__private-button-img {
  padding: 0;
  margin-right: 15px;
}

.project__gh-button-img {
  background-color: white;
  padding: 0;
  border: 2px solid white;
  border-radius: 99px;
  margin-right: 15px;
}

.project__gh-button:hover {
  background-color: white;
  color: var(--github-color);
  border-color: var(--github-color);
}

/* =============== EDUCATION AND EXPERICE SECTIONS =============== */
.education, .experience {
  background-color: #ccecd1;
  padding: calc(var(--menu-height) + 20px) 35px;
  transition: all 1s;
}

.education:hover {
  background-color: #aaf0b3;
}

.education__item, .experience__item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
  padding: 25px 0;
}

.education__item-img, .experience__item-img {
  width: 145px;
}

/* =============== EXPERIENCE SECTION =============== */
.experience {
  background-color: #d7d0f0;
}

.experience:hover {
  background-color: #c1b3f5;
}

/* =============== CONTACT SECTION =============== */
.contact {
  padding: 55px 0;
  background-color: #f0e6b0;
  padding: 25px;
  transition: all 1s;
}

.contact:hover {
  background-color: #f0dd70;
}

.contact__content {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.contact__message  {
  margin: 25px 0;
}

.contact__icon {
  font-size: 40px;
  padding: 20px;
  border: 3px solid black;
  border-radius: 99px;
}

.contact__item {
  text-decoration: none;
  color: black;
  transition: all .5s;
}

.contact__item:hover {
  opacity: .4;
}

/* =============== FOOTER =============== */
.footer {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
  background-color: #171e25;
  color: white;
}

.footer__icon {
  width: 35px;
}

/* =============== RESPONSIVE =============== */
@media screen and (max-width: 1050px) {
  .projects__list {
    grid-template-columns: auto auto;
  }
}

@media screen and (max-width: 800px) {
  .project__img {
    height: 85px;
  }

  .project__frameworks {
    grid-template-columns: auto auto;
  }
}

@media screen and (max-width: 670px) {
  .menu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  
  .menu__open-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    min-height: var(--menu-height);
    transition: all 1s;
  }

  .menu__open-button:hover {
    background-color: white;
    color: #3e3074;
  }
  
  .menu__options {
    position: absolute;
    right: -35%;
    padding: 25px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 25px;
    height: fit-content;
    width: 35%;
    background-color: #2f274d;
    transition: all 1s;
  }

  .menu__options--visible {
    right: 0;
  }

  .menu__item {
    border: none;
  }

  .menu__item:hover {
    transform: translateX(-10px);
    color: var(--highlight-color);
  }

  .menu__item--img:hover {
    transform: translateX(5px);
  }

  .banner__greet {
    font-size: 25px;
  }

  .banner__welcome {
    font-size: 20px;
  }

  .projects__list {
    grid-template-columns: auto;
    margin: 15px 10px;
  }

  .project__frameworks {
    grid-template-columns: auto auto auto;
  }

  .education__item, .experience__item {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 600px) {
  .banner__header {
    flex-direction: column;
    gap: 25px;
  }
  
  .banner__github-logo {
    margin-left: 0px;
  }

  .banner__skills {
    flex-direction: column;  
    gap: 25px;
  }

  .banner__service {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 25px
  }

  .banner__service-text {
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 500px) {
  .contact__content {
    display: grid;
    grid-template-columns: auto auto;
    gap: 25px
  }
}